<section class="section section__cards"> <div class="section__head"><h2 class="section__title">Играй</h2><a class="section__deep" href="#"><span class="deep__text">В раздел</span><img class="deep__img" src="../assets/img/orange_arrow.svg" alt="В раздел"></a></div><div class="cards__wrapper"> <div class="card" v-for="card in cardsInfo"><div class="card__wrapper"> <img class="card__image" :src="card.src" :alt="card.title"><div class="card__info"> <h3 class="card__title">{{ card.title }}</h3><span class="card__text">{{ card.text }}</span><button class="card__button"> <span class="button__text">{{ card.button }}</span><img class="button__arrow" src="../assets/img/white_arrow.svg" alt="Подробнее"></button></div></div></div></div></section>